/**
 * rbgrgba
 * @param color rgb
 * @param alpha 
 * @returns {string} rgba
 */
export function colorToString(color,alpha = 1){
  let r = color[0]
  if (r != (r | 0)) {
    r = (r + 0.5) | 0
  }
  
  let g = color[1]
  if (g != (g | 0)) {
    g = (g + 0.5) | 0
  }
  
  let b = color[2]
  if (b != (b | 0)) {
    b = (b + 0.5) | 0
  }
  
  return `rgba(${r}, ${g} , ${b} , ${alpha} )`
}

export default {
  colorToString
}


