// The BaseObject is a base class that only can be inherited, not be instanced.
import Event from './event'
import {getUid} from '../utils/counter'
import {EventType} from '../meek/eventtype'

/**
 * 
 *
 *  id  revision,id 
 *
 * @class BaseObject
 * @extends Event
 * @constructor
 * @module core
 */
export default class BaseObject extends Event {

  /**
   * Create a BaseObject
   *
   * 
   *
   * @consructor
   */
  constructor () {
    super()
  
    /**
     * ID
     *
     * @property id
     * @type {Number}
     * @private
     */
    this._id = getUid()
  
    /**
     * 
     *
     * @property revision
     * @type {Number}
     * @private
     */
    this._revision = 0
  }
  
  /**
   * map
   *
   * @example this.changed() | super.changed()
   *
   * @method changed
   */
  changed () {
    ++this._revision
    this.dispatchEvent(EventType.CHANGE)
  }

  /**
   *
   * IDID
   *
   * @property id
   * @type {Number}
   */
  get id () {
    return this._id
  }
  
  /**
   *
   * 
   *
   * @property revision
   * @type {Number}
   * @returns {Number} the revision id
   */
  get revision () { return this._revision }
  
}


