import Geometry from './geometry'
import Extent from './extent'
import {ExtentUtil} from './support/extentutil'
import {squaredDistance} from './support/geometryutil'


/**
 * 
 *
 * @class Point
 * @extends Geometry
 * @module geometry
 * @constructor
 * @example
 *
 *    var point = new Datatang.Point(100, 100)
 */
export default class Point extends Geometry {

  /**
   * xy
   * @param x 0
   * @param y 0
   * @constructor
   */
  constructor (xValue, yValue) {
    super()

    this.x = xValue
    this.y = yValue
  }

  /**
   * 
   *
   * @protected
   */
  get geometryType () { return Geometry.POINT }

  /**
   * 
   *
   * @note 
   * @returns {null}
   */
  get extent () {
    if (!this._extent) {
      const extentObj = ExtentUtil.createScaleExtent(this, 8)
      this._extent = new Extent(extentObj[0], extentObj[1], extentObj[2], extentObj[3])
    }
    
    return this._extent
  }

  /**
   *  X 
   *
   * @property x
   * @type x {number}
   */
  get x () { return this._x }
  set x (value) { this._x = value }
  
  /**
   *  Y 
   *
   * @property y
   * @type {number}
   */
  get y () { return this._y }
  set y (value) { this._y = value }

  /**
   * x,y
   *
   * @method offset
   * @param nx x
   * @param ny y
   * @returns {Datatang.Point} 
   */
  offset (nx, ny) {
    return new Point(this.x + nx, this.y + ny)
  }
  
  /**
   * containsXY
   * @param x
   * @param y
   * @param opt
   * @returns {boolean}
   */
  containsXY (x, y, opt = {}) {
    const tolerance = opt.tolerance ? opt.tolerance : 2
    const pointA = this

    const distance = squaredDistance(pointA.x, pointA.y, x, y)
    if (Math.sqrt(distance/2) <= tolerance) {
      return true
    }

    return false
  }

  /**
   * x,y
   *
   * @method update
   * @param nx x
   * @param ny y
   */
  update (nx, ny) {
    this.x = nx
    this.y = ny
    
    this._extent = null
  }
  
  /**
   *
   *
   * getFlatInteriorPoint
   * @returns {[]}
   */
  getFlatInteriorPoint () {
    return this.getCoordinates()
  }
  
  /**
   * 
   *
   * @method getCoordinates
   * @returns {Array}
   */
  getCoordinates () {
    return [this.x, this.y]
  }

  /**
   * 
   *
   * @method getFormShowPosition
   * @param {Number} offsetX x
   * @param {Number} offsetY y
   * @return {[]}
   */
  getFormShowPosition (offsetX = 0, offsetY = 10) {
    return [this.x - offsetX, this.y - offsetY]
  }

  /**
   * 
   *
   * @method setCoordinates
   * @param coordinates
   */
  setCoordinates (coordinates) {
    this.update(coordinates[0], coordinates[1])
    this.changed()
  }
  
  /**
   * getCoordinateIndex
   * @returns {number}
   */
  getCoordinateIndex () {
    return 0
  }
  
  /**
   * 
   *
   * 
   *
   * @method equal
   * @param geometry
   * @return {Boolean}
   */
  equal (geom) {
    let equals = false
    if (geom !== null) {
      equals = ((this.x === geom.x && this.y === geom.x) ||
      (Number.isNaN(this.x) && Number.isNaN(this.y) && Number.isNaN(geom.x) && Number.isNaN(geom.y)))
    }
    return equals
  }
  
  /**
   * 
   *
   * @method clone
   * @returns {Point}
   */
  clone () {
    return new Point(this.x, this.y)
  }
}



