import BaseStyle from './basestyle'
import TextStyle from './textstyle'

/**
 *
 * <b></b><b></b>
 * 
 *
 * @class FillStyle
 * @extends BaseStyle
 * @module style
 * @constructor
 * @example
 *
 *     var fillStyle = new Datatang.FillStyle([255,255,255],
 *         new Datatang.LineStyle([0,0,255],1,1.25),0.5)
 *
 */
export default class FillStyle extends BaseStyle {

  /**
   * 
   * 
   *
   * @constructor
   * @param style 
   * @param color 
   * @param alpha 
   * @param borderStyle LineStyle
   */
  constructor (color = [255,255,255], borderStyle = null, alpha = 1, style = FillStyle.SOLID) {
    super(color, style, alpha)

    this._borderStyle = borderStyle
    this.textStyle = new TextStyle()
  }
  
  /**
   * 
   *
   * @property borderStyle
   * @type {Datatang.LineStyle}
   */
  get borderStyle () { return this._borderStyle}
  set borderStyle (value) { return this._borderStyle = value }
  
  /**
   * 
   *
   * @method clone
   * @returns {FillStyle}
   */
  clone () {
    const newStyle = new FillStyle(this.color, this.borderStyle.clone(),
      this.alpha, this.style)
    
    if (this.textStyle) {
      newStyle.textStyle = this.textStyle
    }
    
    return newStyle
  }
  
}

/**
 * 
 * @static
 * @final
 * @property SOLID
 * @type {string}
 */
FillStyle.SOLID = 'solid'


