import BaseStyle from './basestyle'
import TextStyle from './textstyle'

/**
 * 
 *
 * @class PointStyle
 * @extends BaseStyle
 * @module style
 * @constructor
 * @example
 *
 *  // 
 *
 *  var pointStyle = new Datatang.PointStyle(10,[255, 255, 255],1,new Datatang.LineStyle([0, 0, 255],1,1))
 *
 */
export default class PointStyle extends BaseStyle {

  /**
   * 
   *
   * @constructor
   * @param style {circle|square|cross|x|triangle}
   * @param size 15
   * @param color
   * @param alpha
   * @param xoffset
   * @param yoffset
   * @param angle
   */
  constructor (size = 15, color = [255,0,0], alpha = 1,borderStyle = null,
               xoffset = 0, yoffset = 0, angle = 0, style = PointStyle.CIRCLE) {
    super(color, style, alpha)

    this._size = size
    this._xoffset = xoffset
    this._yoffset = yoffset
    this._angle = angle
    this._borderStyle = borderStyle
    this.textStyle = new TextStyle()
  }

  /**
   * 
   *
   * @method clone
   * @returns {PointStyle}
   */
  clone () {
    const newStyle = new PointStyle(this.size, this.color, this.alpha,
       this.borderStyle.clone(), this.xoffset, this.yoffset, this.angle, this.style)

    if (this.textStyle) {
      newStyle.textStyle = this.textStyle
    }

    return newStyle
  }
  
  /**
   * 
   *
   * @property size
   * @type {Number}
   */
  get size(){ return this._size }
  set size(value){ this._size = value}
  
  /**
   * X 
   *
   * @property xoffset
   * @type {Number}
   */
  get xoffset(){ return this._xoffset }
  set xoffset(value){ this._xoffset = value}
  
  /**
   * Y 
   *
   * @property yoffset
   * @type {Number}
   */
  get yoffset(){ return this._yoffset }
  set yoffset(value){ this._yoffset = value}
  
  /**
   * 
   *
   * @property angle
   * @type {Number}
   */
  get angle(){ return this._angle }
  set angle(value){ this._angle = value}
  
  /**
   * 
   *
   * @property borderStyle
   * @type {LineStyle}
   */
  get borderStyle(){ return this._borderStyle }
  set borderStyle(value){ this._borderStyle = value}
  
}

/**
 * 
 *
 * @static
 * @final
 * @property CIRCLE
 * @type {String}
 */
PointStyle.CIRCLE = 'circle'

/**
 * 
 *
 * @static
 * @final
 * @property SQUARE
 * @type {String}
 */
PointStyle.SQUARE = 'square'

/**
 * 
 *
 * @static
 * @final
 * @property CROSS
 * @type {string}
 */
PointStyle.CROSS = 'cross'

/**
 * X
 *
 * @static
 * @final
 * @property X
 * @type {string}
 */
PointStyle.X = 'x'

/**
 * 
 *
 * @static
 * @final
 * @property TRIANGLE
 * @type {string}
 */
PointStyle.TRIANGLE = 'triangle'



